.input-register {
  border-radius: 10px !important;
}
.input-icon {
  position: relative;
}
.fa-user::before {
  content: "";
  position: absolute;
  color: rgba(157, 154, 154, 0.95);
  top: 41%;
  transform: translateY(-50%);
  font-size: 13px;
  padding-left: 10px;
  padding-right: 15px;
  z-index: 1;
}
.fa-key::before {
  content: "";
  position: absolute;
  color: rgba(157, 154, 154, 0.95);
  top: 41%;
  transform: translateY(-50%);
  font-size: 13px;
  padding-left: 10px;
  padding-right: 15px;
  z-index: 1;
}
.type-item i {
  font-size: 2.8rem;
}
.text-type-item {
  display: block;
}
.text-type-select {
  font-size: 1.1rem;
}
.type-selected i {
  padding: 20px;
  border-radius: 50%;
  background-color: #26b9f2;
  color: #fff;
}
.type-selected span {
  color: #23afea;
}
.type-no-select i {
  padding: 20px;
  border-radius: 50%;
  background-color: #fff;
  color: #26b9f2;
}
.type-no-select span {
  color: #fff;
}
.text-footer {
  font-size: 1.2rem;
  text-transform: uppercase;
  color: #25b7f0;
}
.footer-alt {
  left: 0 !important;
  text-align: center;
  background-color: transparent !important;
}
.footer {
  bottom: 0;
  padding: 19px 15px 20px;
  position: absolute;
  right: 0;
  color: #98a6ad;
  left: 240px;
  background-color: #eeeff3;
}
